export default {
	data() {
		return {
			// 自定义导航栏
			height:"",
			background:{
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			// 自定义导航栏
			data:{}
		}
	},
	onLoad(event) {
		// TODO 后面把参数名替换成 payload
		const payload = event.serverDate;
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.data= JSON.parse(decodeURIComponent(payload));
			console.log("详情页参数");
			console.log(this.data);
		} catch (error) {
			this.data = JSON.parse(payload);
			console.log("详情页参数");
			console.log(this.data);
		}
	},
	methods: {
		start(){
			let that = this;
			uni.showModal({
				  title: '提示',
				    content: '订单服务已结束,订单完成后可到订单中心查看结款状态',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							that.getAccomplish(that.data.code);
							uni.navigateTo({
								url:"../order/order"
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
			})
		},
		//拉去用户订单完成信息
		getAccomplish(code){
			
			uni.request({
				url:this.$apiUrl+"work/accomplish",
				method:"POST",
				dataType:JSON,
				data:{
					uid:this.data.uid,
					code:code,
				},
				success(res) {
					console.log("完成订单");
					console.log(res);
				},
				fail(res) {
					console.log(res);
					uni.showToast({
						title:"服务器无响应"
					})
				}
			})
		}
	},
	//自定义分享页面
	onShareAppMessage(e){
		return {
			title: this.$overShare.title,
			path: this.$overShare.path,
			imageUrl:this.$overShare.imageUrl,
			
		}
	}
}
